  width: 0.84em;
  height: 0.84em;
  clip: rect(0em, 1em, 1em, 0em);
  border-radius: 50%;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

#custom-css-product .c100.p100 .bar:after,
#custom-css-product .c100.p100 .fill {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

#custom-css-product .c100 {
  position: relative;
  font-size: 120px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  float: left;
  margin-right: 10px;
  background-color: #cccccc;
}

#custom-css-product .c100 *,
#custom-css-product .c100 *:before,
#custom-css-product .c100 *:after {
  box-sizing: content-box;
}

#custom-css-product .c100.small {
  font-size: 45px;
}

#custom-css-product .c100 > span {
  position: absolute;
  width: 100%;
  z-index: 1;
  left: 0;
  top: 0;
  width: 2.5em;
  line-height: 2.5em;
  font-size: 0.4em;
  color: #449e44;
  display: block;
  text-align: center;
  white-space: nowrap;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
#custom-css-product .c100:after {
  position: absolute;
  top: 0.08em;
  left: 0.08em;
  display: block;
  content: " ";
  border-radius: 50%;
  background-color: #fff;
  width: 0.84em;
  height: 0.84em;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
}
#custom-css-product .c100 .slice {
  position: absolute;
  width: 1em;
  height: 1em;
  clip: rect(0em, 1em, 1em, 0.5em);
}
#custom-css-product .c100.p100 .slice {
  clip: rect(auto, auto, auto, auto);
}

#custom-css-product .c100.p100 .bar {
  -webkit-transform: rotate(360deg);
